
body {
  background-image: -moz-linear-gradient(left, #02132d, #01193a) !important;
  background-image: -o-linear-gradient(left, #02132d, #01193a) !important;
  background-image: linear-gradient(to right, #02132d, #01193a) !important;
}
.order-content {
  width: 100%;
  position: relative;
}
.order-content .bg {
  width: 100%;
}
.order-content .teamInfo {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}
.order {
  width: 100%;
}
.order .goback-nav {
  width: 100%;
  background: #000;
  height: .88rem;
  display: block;
  text-align: center;
  color: #878787;
  font-size: .26rem;
  line-height: .88rem;
  position: relative;
}
.order .goback-nav img {
  width: .14rem;
  position: absolute;
  top: 50%;
  left: .38rem;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
       -o-transform: translateY(-50%);
          transform: translateY(-50%);
}
.order .order-info {
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-content: center;
      -ms-flex-line-pack: center;
          align-content: center;
  margin: .62rem 0 0;
}
.order .order-info .logo {
  text-align: center;
}
.order .order-info .logo img {
  width: 1rem;
  max-height: 1rem;
  margin-bottom: .4rem;
}
.order .order-info .logo .name {
  font-size: .36rem;
  font-weight: bold;
  color: #fff;
  width: 2.4rem;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.order .order-info .team-info {
  text-align: center;
  margin: 0 .2rem;
}
.order .order-info .team-info b {
  display: block;
  font-size: .48rem;
  color: #fff;
  margin: .42rem 0 .26rem;
}
.order .order-info .team-info span {
  font-size: .24rem;
  color: #8b88a7;
}
.order .goDownload {
  color: #fffefe;
  font-size: .26rem;
  width: 100%;
  position: relative;
  display: block;
}
.order .goDownload img {
  width: 4.28rem;
  display: block;
  margin: 0 auto;
}
.order .goDownload span {
  position: absolute;
  top: 43%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}



